.searchInput {
    width: 100%;
    height: max-content;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 0;
}

.input__container {
    position: relative;
    background: rgba(255, 255, 255, 0.664);
    padding: 10px 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
    border-radius: 22px;
    transition: transform 400ms;
    transform-style: preserve-3d;
    perspective: 500px;
    width: 400px;
}

.shadow__input {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    bottom: 0;
    z-index: -1;
    filter: blur(30px);
    border-radius: 20px;
    background-color: #999cff;
}

.input__button__shadow {
    cursor: default;
    border: none;
    background: none;
    transition: transform 400ms, background 400ms;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    padding: 5px;
}

.input__search {
    width: 100%;
    border-radius: 20px;
    outline: none;
    border: none;
    padding: 8px;
    position: relative;
}

.gradientCards {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 2rem;
    height: max-content;
    min-height: 100vh;
    width: 100%;
    justify-content: center;
    position: relative;
    padding: 3rem 0 !important;
}

.gradientCard {
    position: relative !important;
    width: 100px !important;
    height: 100px !important;
    border-radius: 50% !important;
}

.gradientCardDiv {
    height: 260px;
    width: 273px;
    border-radius: 30px !important;
    position: relative;
    background-color: #000;
    box-shadow: 6px 15px rgba(36, 37, 38, 0.08);
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.gradientCardDivHeader {
    width: 100%;
    position: absolute;
    inset: auto 0 0 0;
    margin: auto;
    padding: 1rem;
}

.gradientCards {
    padding: 20px;
}

.gradientName {
    text-align: left !important;
    font-size: 11px !important;
    position: absolute;
    left: 0;
    top: 0;
    padding: 1rem;
}

.ReactPaginate {
    display: flex;
    justify-content: center;
    align-items: center;
}

.pagination {
    display: flex;
    justify-content: center;
    list-style-type: none;
    margin: 0;
    padding: 0;
    bottom: -40px;
    left: 50%;
    z-index: 1;
    background-color: var(--color-background);
}

.pagination li {
    display: inline-block;
    margin: 5px;
    cursor: pointer;
}

.pagination li a {
    display: block;
    padding: 4px 8px;
    text-decoration: none;
    color: var(--color-headline);
    background-color: var(--color-background);
    border: 1px solid var(--color-secondary);
    border-radius: 4px;
    transition: background-color 0.3s;
}

.pagination li a:hover {
    background-color: var(--color-secondary);
    color: var(--color-main);
    border-color: var(--color-secondary);
}

.pagination.active a {
    background-color: var(--color-button);
    color: var(--color-button-text);
    border-color: var(--color-button);
}

.pagination.disabled a {
    cursor: not-allowed;
    opacity: 0.5;
}

#searchMessage {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 22px;
    width: 100%;
}

#copyButton {
    position: absolute;
    top: 0;
    right: 0;
    width: max-content !important;
    height: max-content !important;
    padding: 1rem;
    background-color: transparent !important;
    border: none !important;
    cursor: pointer;
}

.ContentCopyIcon {
    color: var(--color-button-text);
    transition: all 0.3s ease 0s;
}

.ContentCopyIcon:hover {
    color: var(--color-paragraph);
    transition: all 0.3s ease 0s;
}

@media (max-width: 768px) {
    .searchInput {
        justify-content: start;
    }

    .gradientCardDiv {
        width: 100%;
    }
}
